
import { defineComponent, ref } from "vue";
import Dialog from "../lib/dialog/Dialog.vue";
import {openDialog} from "../lib/dialog/openDialog"
export default defineComponent({
  name: "",
  components: {
    Dialog,
  },
  props: {},
  setup() {
    const visibility = ref(false);
    const closeOnClickModal = ref(true);
    const toggle = () => {
      visibility.value = !visibility.value;
    };
    const handleOk = () => {
      visibility.value = false;
    };
    const beforeOk = () => {
      return true;
    }
    const beforeCancel = () => {
      return true;
    }
    const handleCancel = () => {
      visibility.value = false;
    };
    const showDialog = () => {
      openDialog({
        title: "提示111",
        content: "这是一个提示信息",
        beforeOk: () => {
          return true
        }
      })
    }
    return {
      visibility,
      toggle,
      closeOnClickModal,
      handleOk,
      handleCancel,
      showDialog,
      beforeOk,
      beforeCancel
    };
  },
});
