
import { defineComponent, ref } from "vue";
import Switch from "../../lib/switch/Switch.vue";
export default defineComponent({
  name: "SwitchDemo",
  components: {
    Switch,
  },
  props: {},
  setup() {
    const checked = ref(true);
    const activeColor = ref("red");
    const inactiveColor = ref("green");
    return {
      checked,
      activeColor,
      inactiveColor,
    };
  },
});
