
import { defineComponent, PropType } from "vue";
type beforeFuncType = () => boolean;
export default defineComponent({
  name: "Dialog",
  components: {},
  props: {
    visibility: Boolean,
    closeOnClickModal: {
      type: Boolean,
      deafult: true,
    },
    title: {
      type: String,
      default: "标题",
    },
    beforeOk: {
      type: Function as PropType<beforeFuncType>,
      default: () => {
        return true;
      },
    },
    beforeCancel: {
      type: Function as PropType<beforeFuncType>,
      default: () => {
        return true;
      },
    },
  },
  setup(props, context) {
    const handleClose = () => {
      context.emit("update:visibility", false);
    };
    const onClickModal = () => {
      if (props.closeOnClickModal) {
        handleClose();
      }
    };
    const handleCancel = () => {
      if (props.beforeCancel && props.beforeCancel()) {
        handleClose();
      }
    };
    const handleOk = () => {
      if (props.beforeOk && props.beforeOk()) {
        handleClose();
      }
    };
    return {
      handleClose,
      onClickModal,
      handleCancel,
      handleOk,
    };
  },
});
